import { useTranslation } from "react-i18next";
import style from "../../Shared/StaffStudentProfile/StaffStudentProfile.module.css";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useCallback } from "react";
import {
  assestsUserAdmissionUrl,
  assestsUtilityUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { useStudentAdmissionFeeCount } from "../../Hooks/ApiHooks/StudentProfile/student-manager-api";

const StudentAllTotalAdmissionFees = ({ accessAs }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();

  const { studentAdmissionFeeCount } = useStudentAdmissionFeeCount({
    sid: getQuery.state?.sid,
    skip: !getQuery.state?.sid,
  });
  const onNavigate = useCallback(() => {
    navigate(`/q/${params.username}/student/profile`, {
      state: getQuery.state,
    });
  }, [getQuery.state, navigate, params.username]);

  // console.info("studentAdmissionFeeCount", studentAdmissionFeeCount);
  return (
    <div className={style.sataf_container}>
      {authorized[accessAs] ? null : (
        <div className={style.card_hold_container} onClick={onNavigate}>
          <img
            src={`${assestsUtilityUrl}/profile-view.svg`}
            alt="forword link"
          />
          <h6>{t("view_profile")}</h6>
        </div>
      )}

      <section
        className={style.sataf_container_count}
        // style={{
        //   width: authorized[accessAs] ? "100%" : "auto",
        // }}
      >
        <div
          className={style.sataf_container_count_inner}
          style={{
            gap: "0.8rem",
          }}
        >
          <h6
            style={{
              fontWeight: "300",
              fontSize: "12px",
              marginTop: "-0.4rem",
            }}
          >
            {t("in_inr")}
          </h6>
          <div
            className={style.satafcci_text}
            style={{
              marginTop: "-0.2rem",
            }}
          >
            <img
              src={`${assestsUserAdmissionUrl}/student.svg`}
              alt="gov icon"
            />
            <h6>{t("student")}</h6>
          </div>
          <div
            className={style.satafcci_text}
            style={{
              marginTop: "0.2rem",
            }}
          >
            <img src={`${assestsUserAdmissionUrl}/govt.svg`} alt="gov icon" />
            <h6>{t("govt")}</h6>
          </div>
          <div className={style.satafcci_text}>
            <img src={`${assestsUserAdmissionUrl}/total.svg`} alt="gov icon" />
            <h6>{t("o_total")}</h6>
          </div>
        </div>
        <div className={style.sataf_container_count_inner}>
          <h6
            style={{
              textAlign: "center",
            }}
          >
            {t("o_total")}
          </h6>
          <div className={style.satafcci_amount}>
            <h6>{studentAdmissionFeeCount?.total_app_fees ?? 0}</h6>
          </div>
          <div className={style.satafcci_amount}>
            <h6>{studentAdmissionFeeCount?.total_gov_fees ?? 0}</h6>
          </div>
          <div className={style.satafcci_amount}>
            <h6>{studentAdmissionFeeCount?.total_fees ?? 0}</h6>
          </div>
        </div>
        <div className={style.sataf_container_count_inner}>
          <h6
            style={{
              textAlign: "center",
            }}
          >
            {t("paid")}
          </h6>
          <div className={style.satafcci_amount}>
            <h6>{studentAdmissionFeeCount?.total_app_paid_fees ?? 0}</h6>
          </div>
          <div className={style.satafcci_amount}>
            <h6>{studentAdmissionFeeCount?.total_gov_paid_fees ?? 0}</h6>
          </div>
          <div className={style.satafcci_amount}>
            <h6>{studentAdmissionFeeCount?.total_paid_fees ?? 0}</h6>
          </div>
        </div>
        <div className={style.sataf_container_count_inner}>
          <h6
            style={{
              textAlign: "center",
            }}
          >
            {t("os")}
          </h6>
          <div className={style.satafcci_amount}>
            <h6
              style={{
                fontWeight: "600",
              }}
            >
              {studentAdmissionFeeCount?.total_app_os_fees ?? 0}
            </h6>
          </div>
          <div className={style.satafcci_amount}>
            <h6>{studentAdmissionFeeCount?.total_gov_os_fees ?? 0}</h6>
          </div>
          <div className={style.satafcci_amount}>
            <h6
              style={{
                fontWeight: "600",
              }}
            >
              {studentAdmissionFeeCount?.total_os_fees ?? 0}
            </h6>
          </div>
        </div>
      </section>
    </div>
  );
};

export default StudentAllTotalAdmissionFees;

const authorized = {
  student: true,
};
