import { useTranslation } from "react-i18next";
import { useState } from "react";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import style from "./StudentAdmissionFees.module.css";

import Switch from "@mui/material/Switch";
import { useStudentAdmissionFeeCardInstallEditToggle } from "../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import CreateButton from "../../Ui/Button/CreateButton";
import AdmissionNewFeesCardConfig from "./AdmissionNewFeesCard/AdmissionNewFeesCardConfig";
import StudentProfileSubjectChange from "./StudentProfileSubjectChange/StudentProfileSubjectChange";
const label = { inputProps: { "aria-label": "Switch demo" } };

const StudentOtherStuff = ({
  instituteId,
  isGranted,
  admissionId,
  financeId,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [studentAdmissionFeeCardInstallEditToggle] =
    useStudentAdmissionFeeCardInstallEditToggle();

  const onInstallmentToggle = (e) => {
    if (getQuery.state?.sid && (instituteId || getQuery.state?.instituteId)) {
      setDisabled((pre) => !pre);
      studentAdmissionFeeCardInstallEditToggle({
        flow:
          getQuery.state?.viewAs === "FINANCE_MANAGER"
            ? "Finance_Manager"
            : getQuery.state?.viewAs === "INSTITUTE_ADMIN"
            ? "Institute_Admin"
            : "Admission_Admin",
        editAccess: {
          id: instituteId ? instituteId : getQuery.state?.instituteId,
          sid: getQuery.state?.sid,
          online_amount_edit_access: e.target.checked
            ? "Granted"
            : "Not Granted",
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onCloseStep = () => {
    setStep("");
  };
  const onAddNewFeeCard = () => {
    if (instituteId && admissionId && getQuery.state?.sid) setStep(1);
  };
  const onSubjectChange = () => {
    navigate(`/q/${params.username}/edit/student`, {
      state: {
        ...getQuery.state,
        openAs: "CHANGE_SUBJECT",
      },
    });
  };
  const onAllottmentSubjectChange = () => {
    setStep(2);
  };
  return (
    <>
      {getQuery.state?.onlyReadView === "ONGOING_VIEW_ACCESS" ? null : (
        <div className={style.af_top_container}>
          <p>
            {t("online_install_edit_fees")}
            <Switch
              {...label}
              checked={isGranted}
              onChange={onInstallmentToggle}
            />
          </p>
          {authorization[getQuery.state?.addNewCard] ? null : (
            <CreateButton
              label="add_admission_card"
              onAction={onAddNewFeeCard}
              customStyle={{
                margin: "0",
                paddingBlock: "0.4rem",
              }}
            />
          )}
          {neq_authorization[getQuery.state?.subjectData?.subjectChange] ? (
            <CreateButton
              label="subject_change"
              onAction={onSubjectChange}
              customStyle={{
                margin: "0",
                paddingBlock: "0.4rem",
              }}
            />
          ) : null}

          {allottment_authorization[
            getQuery.state?.subjectData?.subjectChange
          ] ? (
            <CreateButton
              label="subject_change"
              onAction={onAllottmentSubjectChange}
              customStyle={{
                margin: "0",
                paddingBlock: "0.4rem",
              }}
            />
          ) : null}
        </div>
      )}

      {step === 1 ? (
        <AdmissionNewFeesCardConfig
          onClose={onCloseStep}
          admissionId={admissionId}
          sid={getQuery.state?.sid}
          instituteId={instituteId}
          financeId={financeId}
        />
      ) : step === 2 ? (
        <StudentProfileSubjectChange
          onClose={onCloseStep}
          studentId={getQuery.state?.sid}
          instituteId={instituteId}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentOtherStuff;
const authorization = {
  notAuthorized: true,
};

const neq_authorization = {
  authorized: true,
};

const allottment_authorization = {
  allottment_authorized: true,
};
