import style from "../PlateformUser/PlateformUser.module.css";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../Utils/Functions/nextPage";
import { customDebounce } from "../../Utils/Functions/debounce";
import DepartmentSubjectMasterCard from "./DepartmentSubjectMasterCard";
import QvipleLoader from "../../Loader/QvipleLoader";
import Loading from "../../Loader/Loading";
import EmptyMessage from "../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../Ui/Button/CreateButton";
import { useDepartmentAllSubjectMaster } from "../../Hooks/ApiHooks/StudentProfile/student-manager-api";
const DepartmentSubjectMaster = ({
  title,
  onClose,
  selectedId,
  onSelectStaff,
  instituteId,
  departmentId,
  isMultiSelect,
  onMultiSelect,
  selectList,
  onDone,
}) => {
  const { t } = useTranslation();
  const [searchCopy, setSearchCopy] = useState("");
  const [departmentList, setDepartmentList] = useState([]);
  const { departmentAllSubjectMaster, departmentAllSubjectMasterLoading } =
    useDepartmentAllSubjectMaster({
      data: {
        id: instituteId,
        did: departmentId,
      },
      skip: !instituteId ? !instituteId : !departmentId,
    });

  useEffect(() => {
    if (departmentAllSubjectMaster?.list?.length) {
      setDepartmentList(departmentAllSubjectMaster?.list);
    }
  }, [departmentAllSubjectMaster?.list?.length]);
  const onSearchEvent = (e) => {
    if (
      !departmentAllSubjectMasterLoading &&
      departmentAllSubjectMaster?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      let ft = getMasterFilter(departmentAllSubjectMaster?.list, searchCopy);
      setDepartmentList(ft);
    }
  };
  const onSelect = (value) => {
    if (selectedId === value?._id) {
    } else {
      onSelectStaff(value);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t(title ?? "select_subject_master")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              {isMultiSelect && (
                <CreateButton
                  label={t("select")}
                  count={selectList?.length}
                  onAction={onDone}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              )}
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {departmentList?.map((subject) => (
            <DepartmentSubjectMasterCard
              subject={subject}
              key={subject?._id}
              selectedId={selectedId}
              onSelect={onSelect}
              selectList={selectList}
              isMultiSelect={isMultiSelect}
              onMultiSelect={onMultiSelect}
            />
          ))}
          {departmentAllSubjectMasterLoading ? (
            <Loading />
          ) : searchCopy ? (
            departmentList.length === 0 && (
              <EmptyMessage
                title="No subject found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            departmentAllSubjectMaster?.list?.length === 0 && (
              <EmptyMessage
                title="No subject."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default DepartmentSubjectMaster;

const getMasterFilter = (list, args) => {
  if (list?.length > 0) {
    return list?.filter((val) => {
      if (val?.subjectName?.toLowerCase()?.includes(args?.toLowerCase())) {
        return val;
      } else {
        return null;
      }
    });
  } else {
    return [];
  }
};
