import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useState } from "react";
import { useStudentAdmissionAddNewCard } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import style from "../../PlateformUser/PlateformUser.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
const AdmissionNewFeesCardConfirm = ({
  onClose,
  onCloseParent,
  sid,
  appId,
  structureId,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [studentAdmissionAddNewCard] = useStudentAdmissionAddNewCard();
  const onConfirm = () => {
    if (sid && appId && structureId) {
      setDisabled((pre) => !pre);
      studentAdmissionAddNewCard({
        sid: sid,
        addFees: {
          appId: appId,
          struct: structureId,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onCloseParent();
        })
        .catch({});
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("select_confirm_student")}</h6>
              </div>

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
                className={style.close_icon}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <h6 className={style.comfirm_box}>
              {t("confirm_to_add_admission_fees")}{" "}
            </h6>
            <CreateButton label="confirm" onAction={onConfirm} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionNewFeesCardConfirm;
