import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../PlateformUser/PlateformUser.module.css";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useEffect, useState } from "react";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../Loader/Loading";
import TransferSelectOngoingAppllicationCard from "../../AdmissionManager/OneOngoingAdmission/TransferApplication/TransferSelectOngoingAppllicationCard";
import { useStudentAdmissionNewCardApplicationList } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
const AdmissionNewFeesCardApplication = ({
  onClose,
  onSelect,
  instituteId,
  did,
  bid,
  cmid,
  selectedApplicationId,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [list, setList] = useState([]);
  const {
    studentAdmissionNewCardApplicationList,
    studentAdmissionNewCardApplicationListLoading,
  } = useStudentAdmissionNewCardApplicationList({
    data: {
      id: instituteId,
      did: did,
      bid: bid,
      cmid: cmid,
    },
    skip: !instituteId && !did && !bid && cmid,
  });

  useEffect(() => {
    if (studentAdmissionNewCardApplicationList?.length) {
      setList(studentAdmissionNewCardApplicationList);
    }
  }, [studentAdmissionNewCardApplicationList?.length]);

  const onSearchEvent = (e) => {
    if (e.target.value) {
      let ft = getApplicationSearch(
        studentAdmissionNewCardApplicationList,
        e.target.value
      );
      setList(ft);
      setSearch(e.target.value);
    } else {
      setList(studentAdmissionNewCardApplicationList);
      setSearch("");
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("select_admission_application")}</h6>
              </div>

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
                className={style.close_icon}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <div
              className={style.assign_search_container}
              style={{
                marginBottom: "1rem",
              }}
            >
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>

            {list?.map((application) => (
              <TransferSelectOngoingAppllicationCard
                application={application}
                key={application?._id}
                onSelect={onSelect}
                selectedId={selectedApplicationId}
              />
            ))}

            {studentAdmissionNewCardApplicationListLoading ? (
              <Loading />
            ) : search ? (
              list?.length === 0 && (
                <EmptyMessage
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                  title="No ongoing applicationsfoundrelated this search."
                />
              )
            ) : (
              studentAdmissionNewCardApplicationList?.length === 0 && (
                <EmptyMessage
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                  title="No ongoing applications list."
                />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default AdmissionNewFeesCardApplication;

const getApplicationSearch = (list, search) => {
  let fo = list?.filter((val) => {
    if (val?.applicationName?.toLowerCase()?.includes(search?.toLowerCase()))
      return val;
    else return null;
  });
  return fo;
};
