import { useEffect, useState } from "react";
import {
  useStudentAdmissionFormAllottmentChangeSubject,
  useStudentAllSubjectMaster,
} from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import { useTranslation } from "react-i18next";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import Modal from "../../../Ui/Modal/Modal";
import style from "../../FinanceManager/Income/Income.module.css";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  assestsAcademicDepartmentUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import DepartmentSubjectMaster from "../../DepartmentSubjectMaster/DepartmentSubjectMaster";
const StudentProfileSubjectChange = ({ studentId, onClose, instituteId }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [open, setOpen] = useState("");
  const [state, setState] = useState([]);
  const [objData, setObjData] = useState(null);

  const [studentAdmissionFormAllottmentChangeSubject] =
    useStudentAdmissionFormAllottmentChangeSubject();
  const { studentAllSubjectMaster, studentAllSubjectMasterLoading } =
    useStudentAllSubjectMaster({
      data: {
        sid: studentId,
      },
      skip: !studentId,
    });

  useEffect(() => {
    if (studentAllSubjectMaster?.list?.length > 0) {
      let arr = [];
      for (let i = 0; i < studentAllSubjectMaster?.list?.length; i++) {}
    }
  }, [studentAllSubjectMaster?.list?.length]);
  const onChangeSubjectMaster = (val) => {
    if (val?._id === open?._id) {
    } else {
      let arr = [...state];
      let obj = { ...objData };
      let flag = true;
      for (let dt of arr) {
        if (dt?.old_master === open?._id) {
          flag = false;
          dt.new_master = val?._id;
        }
      }
      if (flag) {
        arr.push({
          old_master: open?._id,
          new_master: val?._id,
        });
      }
      obj[open?._id] = {
        subjectName: val?.subjectName,
        _id: val?._id,
      };
      setState(arr);
      setObjData(obj);
      setOpen("");
    }
  };

  const onUpdateMaster = () => {
    if (studentId && state?.length > 0) {
      setDisabled((pre) => !pre);
      studentAdmissionFormAllottmentChangeSubject({
        sid: studentId,
        bodyParameter: {
          masterList: state,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  //   console.info("open", state);
  return (
    <>
      {!open ? (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("student_subject_master_change")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              {studentAllSubjectMaster?.list?.map((master) => (
                <div
                  className={`${style.card_container} ${style.spsc_student}`}
                  style={{
                    justifyContent: "flex-start",
                    alignItems: "center",
                    padding: "0.5rem 1rem",
                  }}
                  onClick={() => setOpen(master)}
                >
                  <img
                    src={`${assestsAcademicDepartmentUrl}/subject.svg`}
                    loading="lazy"
                    alt="avatar"
                    style={{
                      width: "2rem",
                      height: "2rem",
                      borderRadius: "0",
                    }}
                  />
                  <div className={style.card_container_inner}>
                    <h6
                      style={{
                        marginBottom: "0",
                      }}
                    >
                      {master?.subjectName ?? ""}
                      {objData?.[master?._id] && (
                        <span>
                          {"  ->  "} {objData[master?._id]["subjectName"] ?? ""}
                        </span>
                      )}
                    </h6>
                  </div>
                </div>
              ))}
              <CreateButton label="update" onAction={onUpdateMaster} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : (
        <DepartmentSubjectMaster
          onSelectStaff={onChangeSubjectMaster}
          onClose={() => setOpen("")}
          instituteId={instituteId}
          departmentId={open?.department}
        />
      )}
      {studentAllSubjectMasterLoading && <Loading />}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentProfileSubjectChange;
