import { useTranslation } from "react-i18next";
import Loading from "../../../Loader/Loading";
import style from "../StudentAdmissionFees.module.css";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import {
  assestsFeedUrl,
  assestsNavbarUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import styleAdmission from "../../AdmissionManager/AdmissionManager.module.css";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useInstituteAllStudent } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { useCallback, useState } from "react";
import { customDebounce } from "../../../Utils/Functions/debounce";
import styleFinance from "../../FinanceManager/FinanceManager.module.css";

const StudentAdmissionProfileSearch = ({ onCloseBackdrop }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [searchDubli, setSearchDubli] = useState("");

  const { instituteAllStudent, instituteAllStudentLoading } =
    useInstituteAllStudent({
      data: {
        id: getQuery.state?.instituteId,
        page: 1,
        limit: 10,
        search: search,
      },
      skip: search ? !getQuery.state?.instituteId : true,
    });
  const onDebounce = useCallback(
    customDebounce((val) => {
      setSearch(val);
    }, 1500),
    []
  );
  const onSearchEvent = (e) => {
    setSearchDubli(e.target.value);
    if (e.target.value) onDebounce(e.target.value);
    else {
      setSearch("");
    }
  };

  const onViewApplicantProfile = (val) => {
    navigate(`/q/${params.username}/member/student/admission/profile`, {
      state: {
        ...getQuery.state,
        sid: val?._id,
      },
    });
    onCloseBackdrop();
  };

  //   const onCloseBackdrop = () => {
  //     setSearch("");
  //     setSearchDubli("");
  //   };
  return (
    <div className={style.saps_container}>
      <div
        className={styleFinance.fm_search_full}
        style={{
          position: "sticky",
          top: "0",
        }}
      >
        <section className={styleFinance.fm_search_full_container}>
          <div className={styleFinance.fm_search_full_container_inner}>
            <img
              className={styleFinance.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              value={searchDubli}
              autoFocus
              style={{
                borderTop: "0",
                borderBottomLeftRadius: "0",
                borderBottomRightRadius: "0",
              }}
            />
          </div>
        </section>
      </div>

      {instituteAllStudentLoading ? (
        <Loading />
      ) : (
        instituteAllStudent?.list?.map((student, index) => (
          <div
            className={styleAdmission.apf_container}
            onClick={() => onViewApplicantProfile(student)}
            title="View Admission Fees"
            key={`${student?._id}_${index}`}
          >
            <div className={styleAdmission.apf_container_inner}>
              <img
                alt="Student Avatar"
                src={
                  student?.studentProfilePhoto
                    ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                    : `${assestsFeedUrl}/feed-user-avatar.svg`
                }
                loading="lazy"
              />
              <div className={styleAdmission.apfci_text}>
                <h6>{`${student.studentFirstName ?? ""} ${
                  student.studentMiddleName ? student.studentMiddleName : ""
                } ${student.studentLastName ?? ""}`}</h6>

                <p>
                  {t("gr_no")}
                  {student?.studentGRNO ?? ""}
                  {student?.department?.dName
                    ? `, ${student?.department?.dName}`
                    : ""}
                </p>
              </div>
            </div>
            <div className={styleAdmission.apfci_text}>
              <h6>
                {t("rs_only")}{" "}
                {onTotalStudentFees(
                  student?.applicable_fees_pending,
                  student?.government_fees_pending
                )}
              </h6>
              <p>{t("pending_fees")}</p>
            </div>
          </div>
        ))
      )}

      {!instituteAllStudentLoading &&
        instituteAllStudent?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No student found related this search."
          />
        )}
    </div>
  );
};

export default StudentAdmissionProfileSearch;

const onTotalStudentFees = (arg, arg1) => {
  return arg + arg1;
};
