import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FeeStatisticsDepartmentFilter from "../../FinanceManager/FeeStatistics/FeeStatisticsDepartmentFilter";
import FeeStatisticsBatchFilter from "../../FinanceManager/FeeStatistics/FeeStatisticsBatchFilter";
import FeeStatisticsStandardFilter from "../../FinanceManager/FeeStatistics/FeeStatisticsStandardFilter";
import { useCallback, useEffect, useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import CreateButton from "../../../Ui/Button/CreateButton";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
const AdmissionNewFeesCard = ({
  onClose,
  grabState,
  onGrabIds,
  fid,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    department: "",
    batch: "",
    classMaster: "",
  });
  const [errorField, setErrorField] = useState("");
  useEffect(() => {
    setState({
      department: grabState?.department ?? "",
      batch: grabState?.batch ?? "",
      classMaster: grabState?.classMaster ?? "",
    });
  }, [grabState?.classMaster, grabState?.department, grabState?.batch]);

  const onSelectDepartment = useCallback((value) => {
    setState((prev) => ({
      ...prev,
      department: value,
    }));
  }, []);
  const onSelectBatch = useCallback((value) => {
    setState((prev) => ({
      ...prev,
      batch: value,
    }));
  }, []);

  const onSelectStandard = useCallback((value) => {
    setState((prev) => ({
      ...prev,
      classMaster: value,
    }));
  }, []);
  const onNext = () => {
    let validation = requiredField(state);
    if (existingRequiredField(validation)) {
      onGrabIds(state);
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_admission_card")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FeeStatisticsDepartmentFilter
              fid={fid}
              onSelect={onSelectDepartment}
              onlyDepartment
              errorShow={errorField["department"]}
              defalutValue={state.department?.dName}
            />

            <FeeStatisticsBatchFilter
              did={state.department?._id}
              onSelect={onSelectBatch}
              onlyBatch
              errorShow={errorField["batch"]}
              defalutValue={state.batch?.batchName}
            />
            <FeeStatisticsStandardFilter
              did={state.department?._id}
              onSelect={onSelectStandard}
              instituteId={instituteId}
              errorShow={errorField["classMaster"]}
              defalutValue={state.classMaster?.className}
            />

            <CreateButton label="next" onAction={onNext} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default AdmissionNewFeesCard;
