import { useState } from "react";
import AdmissionNewFeesCardApplication from "./AdmissionNewFeesCardApplication";
import AdmissionNewFeesCardConfirm from "./AdmissionNewFeesCardConfirm";
import AdmissionNewFeesCard from "./AdmissionNewFeesCard";
import SelectFeeStructure from "../../FinanceManager/FeesStructure/SelectFeeStructure/SelectFeeStructure";

const AdmissionNewFeesCardConfig = ({
  onClose,
  admissionId,
  sid,
  instituteId,
  financeId,
}) => {
  const [step, setStep] = useState(1);
  const [state, setState] = useState({
    department: "",
    batch: "",
    classMaster: "",
    application: "",
    structure: "",
  });

  const onGrabIds = (val) => {
    setState((prev) => ({
      ...prev,
      department: val?.department,
      batch: val?.batch,
      classMaster: val?.classMaster,
    }));
    setStep(2);
  };
  const onGrabApplication = (val) => {
    setState((prev) => ({
      ...prev,
      application: val,
    }));
    setStep(3);
  };
  const onGrabStructure = (val) => {
    setState((prev) => ({
      ...prev,
      structure: val,
    }));
    setStep(4);
  };
  return (
    <>
      {step === 1 ? (
        <AdmissionNewFeesCard
          onClose={onClose}
          onGrabIds={onGrabIds}
          grabState={state}
          fid={financeId}
          instituteId={instituteId}
        />
      ) : step === 2 ? (
        <AdmissionNewFeesCardApplication
          onClose={() => setStep(1)}
          onSelect={onGrabApplication}
          instituteId={instituteId}
          aid={admissionId}
          did={state.department?._id}
          bid={state.batch?._id}
          cmid={state?.classMaster?._id}
          selectedApplicationId={state.application?._id}
        />
      ) : step === 3 ? (
        <SelectFeeStructure
          onClose={() => setStep(2)}
          onSelect={onGrabApplication}
          selectedStructure={state.structure?._id ?? ""}
          did={state.department?._id}
          bid={state.batch?._id}
          cmid={state?.classMaster?._id}
          onSelectedStrucutre={onGrabStructure}
          oneStep
          title="select_fee_structure_for_application"
        />
      ) : step === 4 ? (
        <AdmissionNewFeesCardConfirm
          onCloseParent={onClose}
          onClose={() => setStep(3)}
          sid={sid}
          appId={state.application?._id}
          structureId={state.structure?._id}
        />
      ) : null}
    </>
  );
};

export default AdmissionNewFeesCardConfig;
