import { useSelector } from "react-redux";
import { useOneUserProfile } from "../../../hooks/dashboard/dashboard-api";
import { useEffect } from "react";
import style from "./Navbar.module.css";
import InsNavbar from "../../../Dashboard/InsDashboard/Components/Navbar/Navbar";
import Navbar from "../../../Dashboard/UserDashboard/Components/Navbar/Navbar";
const NavbarConfig = ({ isView, children }) => {
  const ids = useSelector((state) => state.idChange.id);

  const { oneUserProfile, oneUserProfileRefetch } = useOneUserProfile({
    data: ids,
    skip: isView === "INSTITUTE" ? true : !ids,
  });
  useEffect(() => {
    if (isView !== "INSTITUTE") {
      oneUserProfileRefetch();
    }
  }, [isView, oneUserProfileRefetch]);
  return (
    <>
      <div className={style.navbar}>
        {isView === "INSTITUTE" ? (
          <InsNavbar status="home" />
        ) : (
          <Navbar
            status="members"
            profileId={oneUserProfile?.user?._id ?? ""}
            username={oneUserProfile?.user?.username}
            name={oneUserProfile?.user?.userLegalName}
            photoId={oneUserProfile?.user?.photoId}
            profilePhoto={oneUserProfile?.user?.profilePhoto}
            profileData={oneUserProfile?.user}
          />
        )}
      </div>

      {children}
    </>
  );
};

export default NavbarConfig;
